<template>
    <div>
        <div class="search-panel bg-gray-lightest p-10">
            <label class="m-r-5">数据日期：</label>
            <el-date-picker
                class="search-date-selector m-r-20"
                v-model="searchForm.dataDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择数据日期">
            </el-date-picker>
            <el-button size="small" class="m-l-30" icon="el-icon-search" type="primary" @click="startSearch">查询
            </el-button>
        </div>
        <div class="handle-panel m-t-10">
            <el-button size="small" icon="el-icon-picture-outline" type="primary" @click="startRunDirective">运行
            </el-button>
        </div>
        <el-table
            ref="settingTable"
            :data="quotaSettings"
            border
            class="m-t-10"
            style="width: 100%"
            :header-cell-style="tableHeadStyle"
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column label="数据日期" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{new Date(scope.row.dataDt) | convertDate}}</span>
                </template>
            </el-table-column>
            <el-table-column label="指标代码" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.indiCode}}</span>
                </template>
            </el-table-column>
            <el-table-column label="指标名称" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.indiName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="调整次序" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.editTimes}}</span>
                </template>
            </el-table-column>
            <el-table-column label="处理指令" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.processInst}}</span>
                </template>
            </el-table-column>
            <el-table-column label="指令类别" header-align="center" align="center">
                <template slot-scope="scope"><!-- 1.基础指标 2.分析指标 -->
                    <span>{{scope.row.instType==1?'基础指标': '分析指标'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="id" label="状态" header-align="center" align="center"></el-table-column>
            <el-table-column label="调度开始时间" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{new Date(scope.row.createDt) | convertDate}}</span>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            v-if="totalCount > 0"
            class="text-right m-t-20"
            layout="prev, pager, next"
            :current-page.sync="page"
            :page-size="pageSize"
            :total="totalCount"
            @current-change="getQuotaSettings"
        >
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: 'QuotaSettings',
        mixins: [],
        components: {},
        data() {
            return {
                searchForm: {
                    dataDate: ''
                },
                tableHeadStyle: {
                    'font-weight': 'bold',
                    'color': '#333'
                },
                quotaSettings: [
                    {
                        "id": 1,
                        "indiId": 15,
                        "indiName": "测试衍生指标X",
                        "indiCode": "10200001002",
                        "isValid": 0,
                        "dataDt": "2018-11-13T08:09:20.000+0000",
                        "startDataDt": null,
                        "endDataDt": null,
                        "processInst": "CALL P_DM_INDI_BOND('#etl_date#','3010003','FOUD_BUSI_001',?,?)",
                        "instType": 2,
                        "createdBy": 2,
                        "createDt": "2018-11-13T07:46:54.000+0000",
                        "lastEditBy": 2,
                        "lastEditDt": "2018-11-14T12:45:32.000+0000",
                        "status": 0,
                        "latestDataDate": null,
                        "editTimes": 5
                    }
                ],
                multipleSelection: [], // 勾选的配置
                page: 1,
                pageSize: 10,
                totalCount: 0
            }
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
        },
        mounted() {
        },
        computed: {},
        methods: {
            getQuotaSettings() {

            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            startSearch() {
                let loadingInstance = this.$loading({});
                setTimeout(() => {
                    loadingInstance.close();
                }, 500);
            },
            /***
             * 开始运行指令
             */
            startRunDirective() {
                if(this.multipleSelection.length === 0) {
                    this.$message.warning('请选择指令!');
                    return false;
                } else {
                    let loadingInstance = this.$loading({});
                    setTimeout(() => {
                        loadingInstance.close();
                        this.$message.success('运行成功!');
                    }, 500);
                }
            }
        },
        filters: {},
        watch: {},
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>

</style>



